ul.list-projects{
    list-style: none;
}

ul.list-projects li{
    padding: 1em;
}

ul.list-projects li a{
    display: block;
    overflow: hidden;
    position: relative;
}

ul.list-projects li a:before{
    content: '';
    display: block;
    padding-top: 100%;
}

ul.list-projects li a span{
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), to(#2c3e50));
    background: -webkit-linear-gradient(transparent, #2c3e50);
    background: -o-linear-gradient(transparent, #2c3e50);
    background: linear-gradient(transparent, #2c3e50);
    color: #fff;
    display: -ms-flexbox;
    display: flex;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
    font-size: 1.2em;
    font-weight: 500;
    opacity: 0;
    padding: 2em;
    position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
    text-align: center;
    -webkit-transition: all 0.25s;
    -o-transition: all 0.25s;
    transition: all 0.25s;
    -webkit-transform: translateY(10%);
        -ms-transform: translateY(10%);
            transform: translateY(10%);
}

ul.list-projects li a:hover span{
    opacity: 1;
    -webkit-transform: translateY(0);
        -ms-transform: translateY(0);
            transform: translateY(0);
}


